@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@900&display=swap');


.home{
    margin: 0;
    padding: 0;
    background-color: #22283e;
    /* height: 200vh; */
    width: 100%;
    margin:auto;
    /* box-sizing: border-box; */
    /* border: 1px solid yellow; */
    
    overflow-x: hidden;
}

/* .home > Navbar{
    margin-left: 50rem;
} */


.photo{
    width: 100%;
     position: relative;
     top: 2rem;
     margin-bottom: 2rem;
     
    /* border: 1px solid greenyellow; */
    /* border-radius: 50%; */
}

.text{
    margin-top: 5%;
    font-size: 50px;
    font-family: 'Poppins', sans-serif;
    color: bisque;
}

.name{
    color: #ffc83d;
}

.intro{
    margin-top: 10rem;
    display: flex;
    justify-content: center;
    /* width: 90%; */
    gap: 4rem;
    /* height: 100vh; */
    /* border: 1px solid red; */
    margin: auto;
    
}

.intro>div{
    /* border:  1px solid palevioletred; */
    margin-top: 4rem;
}

.resume{
    /* font-size: 30px;
    background-color: blueviolet;
    color: antiquewhite;
    border-radius: 5%;
    padding: 2%;
    border: none;
    cursor: pointer; */


    padding: 15px 25px;
    font-size: 24px;
    text-align: center;
    cursor: pointer;
    outline: none;
    color: #fff;
    background-color: #04AA6D;
    /* border: 1px solid red; */
    border-radius: 15px;
    box-shadow: 0 9px #999;

}

.resume:hover {background-color: #3e8e41}

.resume:active {
    background-color: #3e8e41;
    box-shadow: 0 5px #666;
    transform: translateY(4px);
  }


